	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/!/thC/thC_SS12.jpg\" alt=\"Click to see the details.\"></p>\n<p>Till I report my survival. GW? What is it food?</p>\n<p>So why not wait for a while.</p>\n<p>By the way, is there a fee for a dream drama patch? I was flooded with e-mails and applause, but personally I did not have that intention at all, but would you prefer it?</p>\n<h2>Do not give up even if the HDD is broken!</h2>\n<p><a href=\"http://fromto.cc/hosokawa/diary/2004/20040122-mita2/\">Do not give up if the disc does not recognize from the BIOS!</a></p>\n<p>It is kind of interesting. Two HDDs broken in my house are asleep, but if this can salvage with thousands of yen investment it might be amazing. Originally we are giving up on the contents halfway, so if impossible impossible, let's try it for a moment or a degree.</p>\n<h2>16:00 postscript</h2>\n<p>HDD Why I am recognizing from the BIOS wwwwwww Even before I was not even aware BIOS wwww</p>\n<p>However, it is impossible for me to start running on my own. That's why I changed my schedule. From now I will buy nude ww</p>","frontmatter":{"cover":null,"date":"2007-04-28T15:10:00+09:00","strDate":"2007/4/28","title":"pseŭda-kaĝo"}}},"pathContext":{"path":"/en/blog/2007/04/28-pseuda-kago/","langKey":"en"}}

